/*
 * We are grouping all the non-organization accounts under a
 * single fake-organization called "External teams"
 */
export const isExternalTeamsOrganization = (
  organization?: { id: string; name: string } | undefined
) => {
  return organization?.id === 'external-teams';
};

export const createExternalTeamsOrganization = <Account extends { id: string }>(
  accounts: Account[]
) => ({ id: 'external-teams', name: 'External teams', accounts, logo_url: '' });
